import { useState, useEffect, useRef } from 'react';

// ØªØ­Ø³ÙŠÙ† Ø§Ù„Ø³Ø§Ø¹Ø© Ù„Ø¶Ù…Ø§Ù† Ø¯Ù‚Ø© Ø§Ù„ÙˆÙ‚Øª ÙˆÙ…ÙˆØ§Ù‚ÙŠØª Ø§Ù„ØµÙ„Ø§Ø© ÙÙŠ Ø§Ù„Ø£Ø±Ø¯Ù†
// ÙŠØ¯Ø¹Ù… ØªØ­Ø¯ÙŠØ« ÙŠØ¯ÙˆÙŠ Ø¹Ù†Ø¯ ØªØºÙŠÙŠØ± Ø§Ù„Ø¯ÙˆØ§Ù„
export function useClock(timeZone?: string) {
  const [time, setTime] = useState(() => {
    // Ø¥Ø°Ø§ ØªÙ… ØªØ­Ø¯ÙŠØ¯ Ø§Ù„Ù…Ù†Ø·Ù‚Ø© Ø§Ù„Ø²Ù…Ù†ÙŠØ©ØŒ Ù†Ø³ØªØ®Ø¯Ù…Ù‡Ø§ Ù„Ù„ØªØ­Ø¯ÙŠØ«
    if (timeZone) {
      return new Date(new Date().toLocaleString("en-US", { timeZone }));
    }
    return new Date();
  });
  
  const intervalRef = useRef<NodeJS.Timeout | null>(null);

  // Ø¯Ø§Ù„Ø© Ù„ØªØ­Ø¯ÙŠØ« Ø§Ù„ÙˆÙ‚Øª Ø¨Ø´ÙƒÙ„ Ø¯Ù‚ÙŠÙ‚
  const updateTime = () => {
    if (timeZone) {
      // Ù„Ù„Ù…Ù†Ø·Ù‚Ø© Ø§Ù„Ø²Ù…Ù†ÙŠØ© Ø§Ù„Ø£Ø±Ø¯Ù†ÙŠØ© (UTC+3) Ø¨Ø´ÙƒÙ„ Ø¯Ù‚ÙŠÙ‚
      if (timeZone === 'Asia/Amman') {
        const now = new Date();
        const localOffset = now.getTimezoneOffset(); // Ø¯Ù‚Ø§Ø¦Ù‚ ØºØ±Ø¨ UTC
        const jordanOffset = -180; // UTC+3 = -180 Ø¯Ù‚ÙŠÙ‚Ø© ØºØ±Ø¨ UTC
        const totalOffset = jordanOffset - localOffset;
        setTime(new Date(now.getTime() + totalOffset * 60000));
      } else {
        setTime(new Date(new Date().toLocaleString("en-US", { timeZone })));
      }
    } else {
      setTime(new Date());
    }
  };

  // Ø¯Ø§Ù„Ø© Ù„Ø¨Ø¯Ø¡ Ø§Ù„Ø³Ø§Ø¹Ø©
  const startClock = () => {
    // Ù…Ø³Ø­ Ø£ÙŠ interval Ù‚Ø¯ÙŠÙ…
    if (intervalRef.current) {
      clearInterval(intervalRef.current);
    }
    
    // ØªØ­Ø¯ÙŠØ« Ø§Ù„ÙˆÙ‚Øª ÙÙˆØ±Ù‹Ø§
    updateTime();
    
    // Ø¨Ø¯Ø¡ ØªØ­Ø¯ÙŠØ« Ø§Ù„ÙˆÙ‚Øª ÙƒÙ„ Ø«Ø§Ù†ÙŠØ©
    intervalRef.current = setInterval(updateTime, 1000);
  };

  useEffect(() => {
    // Ø¨Ø¯Ø¡ Ø§Ù„Ø³Ø§Ø¹Ø© Ø¹Ù†Ø¯ Ø§Ù„ØªØ­Ù…ÙŠÙ„
    startClock();
    
    // ØªÙ†Ø¸ÙŠÙ Ø¹Ù†Ø¯ Ø¥Ù„ØºØ§Ø¡ Ø§Ù„Ù…ÙƒÙˆÙ†
    return () => {
      if (intervalRef.current) {
        clearInterval(intervalRef.current);
      }
    };
  }, [timeZone]);

  // Ø¯Ø§Ù„Ø© Ù„ØªØ­Ø¯ÙŠØ« ÙŠØ¯ÙˆÙŠ Ù„Ù„Ø³Ø§Ø¹Ø© (Ø¹Ù†Ø¯ Ø§Ù„Ø­Ø§Ø¬Ø© Ù„ØªØºÙŠÙŠØ± Ø§Ù„Ø¯ÙˆØ§Ù„)
  const refreshClock = () => {
    startClock();
  };

  // Ø¥Ø¶Ø§ÙØ© Ø¯Ø§Ù„Ø© Ø§Ù„ØªØ­Ø¯ÙŠØ« Ø¥Ù„Ù‰ ÙƒØ§Ø¦Ù† Ø§Ù„ÙˆÙ‚Øª Ø§Ù„Ø°ÙŠ ÙŠØªÙ… Ø¥Ø±Ø¬Ø§Ø¹Ù‡
  return {
    ...time,
    refreshClock,
    getTime: () => time,
    getTimeString: () => time.toLocaleTimeString(),
    getDateString: () => time.toLocaleDateString()
  };
}
